<template> 
<label class="togglebox">
  <input type="checkbox">
  <span :class="classname"></span>
</label>
</template>

<script>
export default {
  name: "ToggleComponent",
  props: { 
    classname: String,
  },
};
</script>

<style scoped> 
.togglebox {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
 
.togglebox input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The toggle */
.toggle {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #73E8A3;
  -webkit-transition: .4s;
  transition: .4s;
}

.toggle:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .toggle {
  background-color: #73E8A3;
}

input:focus + .toggle {
  box-shadow: 0 0 1px #73E8A3;
}

input:checked + .toggle:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded toggles */
.toggle.round {
  border-radius: 34px;
}

.toggle.round:before {
  border-radius: 50%;
}
</style>